n:0.5s;transition-delay:0.2s;background-color:aqua}.unassigned-user-container{max-width:22rem;padding:0.5rem}.unassigned-user-list{display:grid;grid-gap:0.25rem;gap:0.25rem;grid-template-columns:1fr}.unassigned-list-header-container .display-by-container{display:flex;justify-content:space-between;align-items:center}body{font-family:'Roboto', sans-serif}

